@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Mulish", sans-serif}.navbar-brand{font-size:1.9rem !important;color:yellow}.navbar a{font-size:1.3rem;text-transform:capitalize;font-family:"Potta One", cursive}.menu_active{font-weight:bold;border-bottom:1px solid #565387}.navbar a:hover{color:yellow !important}@media (max-width: 991px){.navbar-brand{font-size:1.5rem !important}}#header{width:100%;height:80vh;margin-top:70px}#header h1{margin:0 0 10px 0;font-size:48 px;font-weight:700;color:black}#header .brand-name{color:#3498db}#header h2{color:#484848;font-size:20px}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1 px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:#3498db;border:2px solid #3498db;text-decoration:none}#header .btn-get-started:hover{background:#3498db;color:#fff}img.vert-move{animation:mover 1s infinite alternate}img.vert-move{animation:mover 1s infinite alternate}@keyframes mover{0%{transform:translateY(0)}100%{transform:translateY(-40px)}}@media screen and (max-width: 800px){.left,.main,.right{width:100%}}#page-container{position:relative;min-height:100vh}#content-wrap{padding-bottom:2.5rem}.Footer{background-color:#a2a9ac;text-align:center;height:30px;font-weight:bold;font-family:"Mulish", sans-serif;position:absolute;bottom:0;width:100%;height:2.5rem}

